.app-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }
  
  .count {
    font-size: 80px;
    font-family: 'Roboto';
    color: #2e3e4e;
    margin-top: 8px;
    margin-bottom: 20px;
  }
  
  .button {
    font-size: 16px;
    font-weight: 400;
    font-family: 'Roboto';
    color: #ffffff;
    padding-top: 8px;
    padding-bottom: 8px;
    padding-right: 15px;
    padding-left: 15px;
    margin: 8px;
    background-color: #7c69e9;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    outline: none;
  }
  